import React from "react";

const ProgressBar = (props) => {
  const { bgcolor, completed, title } = props;
  const containerStyle = {
    backgroundColor: "#e0e0de",
    borderRadius: 50,
    marginBottom: 20,
  };

  const styleTitle = {
    fontSize: "20px",
    textAlign: "left",
  };

  const filterStyles = {
    width: `${completed}%`,
    backgroundColor: bgcolor,
    textAlign: "center",
    transition: "width 1s ease-in-out",
    borderRadius: 50,
  };

  const labelStyle = {
    color: "white",
    fontWeight: "bold",
  };

  return (
    <div>
      <h2 style={styleTitle}>{`${title}`}</h2>
      <div style={containerStyle}>
        <div style={filterStyles}>
          <span style={labelStyle}> {`${completed}%`}</span>
        </div>
      </div>
    </div>
  );
};

export default ProgressBar;
