import Progressbar from "./progress";

const MySkill = () => {
  const myProgress = [
    { bgcolor: "#18152E", completed: 65, title: "Photoshop" },
    { bgcolor: "#330D3E", completed: 80, title: "Adobe Premier Pro" },
    { bgcolor: "#2D3246", completed: 75, title: "Lightroom" },
  ];

  return (
    <section id="skill">
      <div className="container">
        <div className="text-start my-5">
          <h1 className="fw-bolder">My Skill</h1>
          <blockquote className="fw-bold">This Is My Skill</blockquote>
        </div>
        <div className="row align-items-center">
          <div className="col-12">
            {myProgress.map((item, index) => (
              <Progressbar
                key={index}
                bgcolor={item.bgcolor}
                completed={item.completed}
                title={item.title}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default MySkill;
