import React, { useState } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import human from "../images/profile picture.jpg";
import { Modal, ModalBody, ModalHeader } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTwitter,
  faFacebook,
  faInstagram,
  faWhatsapp,
  faLinkedin,
} from "@fortawesome/fontawesome-free-brands";
import "../css/hero.css";

const Hero = () => {
  const [modal, setModal] = useState(false);

  return (
    <section id="home" className="hero ">
      <Container>
        {/* popup */}
        <Modal size="md" isOpen={modal} toggle={() => setModal(!modal)}>
          <ModalHeader toggle={() => setModal(!modal)} className="modal-header">
            <h4 className="text-dark">Contact Me</h4>
          </ModalHeader>

          <ModalBody
            className="modal-body"
            style={{ backgroundColor: "rgb(39, 37, 37)" }}
          >
            <Container>
              <Row>
                {/* Twitter */}
                <div className="d-flex">
                  <FontAwesomeIcon
                    icon={faTwitter}
                    href="#"
                    className="text-white my-2"
                    size="xl"
                  />
                  <span className="mt-2 ms-3 text-white">
                    Contact Me With <a href="#">Twitter</a>
                  </span>
                </div>
                {/* Facebook */}
                <div className="d-flex">
                  <FontAwesomeIcon
                    icon={faFacebook}
                    href="#"
                    className="text-white my-2"
                    size="xl"
                  />
                  <span className="mt-2 ms-3 text-white">
                    Contact Me With <a href="#">Facebbok</a>
                  </span>
                </div>
                {/* Instagram */}
                <div className="d-flex">
                  <FontAwesomeIcon
                    icon={faInstagram}
                    href="#"
                    className="text-white my-2"
                    size="xl"
                  />
                  <span className="mt-2 ms-3 text-white">
                    Contact Me With <a href="#">Instagram</a>
                  </span>
                </div>
                {/* Whatsapp */}
                <div className="d-flex">
                  <FontAwesomeIcon
                    icon={faWhatsapp}
                    href="#"
                    className="text-white my-2"
                    size="xl"
                  />
                  <span className="mt-2 ms-3 text-white">
                    Contact Me With <a href="#">WhatSapp</a>
                  </span>
                </div>
                {/* Linkedin */}
                <div className="d-flex">
                  <FontAwesomeIcon
                    icon={faLinkedin}
                    href="#"
                    className="text-white my-2"
                    size="xl"
                  />
                  <span className="mt-2 ms-3 text-white">
                    Contact Me With <a href="#">Linkedin</a>
                  </span>
                </div>
              </Row>
            </Container>
          </ModalBody>
        </Modal>
        {/* popup */}

        <Row className="container-1 align-items-center justify-content-center">
          <div className="col-lg-7 col-md-6 text-lg-start text-md-center text-center mt-5">
            <h4 className="text-light fs-4 ">Hello , I Am</h4>
            <h1 className="fw-bold fs-1 text-light">JAKOB OWENS</h1>
            <h4 className="fw-light text-light fs-6">
              Freelancer Photography , Videography And Editor
            </h4>

            <button
              className="button-hero rounded-5 mt-3 fw-bold"
              onClick={() => setModal(true)}
            >
              Hire Me
            </button>
          </div>
          <div className="col-lg-5 col-md-8 text-center">
            <img alt="human" className=" w-100 rounded-circle" src={human} />
          </div>
        </Row>
      </Container>
    </section>
  );
};

export default Hero;
