import { Form, Container } from "react-bootstrap";
import React, { useRef } from "react";
import emailjs from "@emailjs/browser";
import "../css/contact.css";

const ContactUs = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_muxmud7",
        "template_thiqpyd",
        form.current,
        "vdYxRctEXIRDskBpN"
      )
      .then(
        (result) => {
          console.log(result.text);
          console.log("Message Sent");
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  return (
    <section id="contact">
      <Container className="mx-auto col-lg-8 my-5">
        <div className="title my-5">
          <h1 className="fw-bolder">Contact Us Via Email</h1>
          <blockquote className="fw-bold">Write Me A Message</blockquote>
        </div>
        <Form ref={form} onSubmit={sendEmail}>
          <Form.Label className="fw-bolder">Name</Form.Label>
          <input
            type="text"
            placeholder="Enter Your Name"
            name="user_name"
            className="w-100 p-2 rounded-2"
          />
          <Form.Label className="fw-bolder">Email address</Form.Label>
          <input
            type="email"
            placeholder="name@example.com"
            name="user_email"
            className="w-100 p-2 rounded-2"
          />
          <Form.Label className="fw-bolder">Message</Form.Label>
          <textarea
            name="message"
            className="w-100 rounded-2 p-2"
            placeholder="Enter Your Message"
            rows={6}
          />
          <button type="submit" value="Send" className="mt-2 mb-5 rounded-5">
            Send Massage
          </button>
        </Form>
      </Container>
    </section>
  );
};

export default ContactUs;
