import React from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";

import "../css/Navigation.css";

const Navigation = () => {
  return (
    <Navbar expand="lg" className="navbar sticky-top">
      <Container>
        <Navbar.Brand
          href="#home"
          className="fst-italic fw-bolder py-2"
          style={{ color: "#33e7ff" }}
        >
          JAKOB
        </Navbar.Brand>
        <Navbar.Toggle className="bg-light" aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link href="#home" className="nav-link text-light mx-3 fw-bold">
              Home
            </Nav.Link>
            <Nav.Link
              href="#about"
              className="nav-link text-light mx-3 fw-bold"
            >
              About
            </Nav.Link>
            <Nav.Link
              href="#skill"
              className="nav-link text-light mx-3 fw-bold"
            >
              Skill
            </Nav.Link>
            <Nav.Link
              href="#portfolio"
              className="nav-link text-light mx-3 fw-bold"
            >
              Portfolio
            </Nav.Link>
            <Nav.Link
              href="#contact"
              className="nav-link text-light mx-3 fw-bold"
            >
              Contact
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Navigation;
