import { useState } from "react";
import { Container, Card } from "react-bootstrap";
import "../css/project.css";
import axios from "axios";

const Project = () => {
  const [image, setImage] = useState([]);
  axios
    .get(
      "https://api.unsplash.com/users/jakobowens1/photos?client_id=PbUlShPsCjtWPfDAtM9UPE-36bpf4r6VsVtKjMeYH2c"
    )
    .then((response) => {
      setImage(response.data);
    });

  return (
    <>
      <section id="portfolio">
        <Container className="my-5">
          <div className="title text-center">
            <h1 className="fw-bolder">My Project</h1>
            <blockquote className="fw-bold">This Is My Project</blockquote>
          </div>
          <div className="row mx-auto ">
            {image.map((value, index) => {
              return (
                <div
                  style={{ width: "22rem" }}
                  className="mx-auto my-3 text-center align-content-center"
                >
                  <Card.Img
                    src={value.urls.small}
                    key={index}
                    className="w-100 h-100"
                  />
                </div>
              );
            })}
          </div>
        </Container>
      </section>
    </>
  );
};

export default Project;
